const express = require('express');
const PaymentController = require('../controllers/PaymentController');
const { authMiddleware } = require('../middleware/authMiddleware');
const { asyncHandler } = require('../utils/asyncHandler');

const router = express.Router();
const paymentController = new PaymentController();

// Middleware de autenticação para todas as rotas
router.use(authMiddleware);

// Rotas de pagamentos
router.get('/', asyncHandler(paymentController.getAllPayments.bind(paymentController)));
router.get('/search', asyncHandler(paymentController.searchPayments.bind(paymentController)));
router.get('/overdue', asyncHandler(paymentController.getOverduePayments.bind(paymentController)));
router.get('/due-today', asyncHandler(paymentController.getPaymentsDueToday.bind(paymentController)));
router.get('/stats', asyncHandler(paymentController.getPaymentStats.bind(paymentController)));
router.get('/revenue-chart', asyncHandler(paymentController.getRevenueChart.bind(paymentController)));
router.get('/export', asyncHandler(paymentController.exportPayments.bind(paymentController)));
router.get('/contract/:contractId', asyncHandler(paymentController.getPaymentsByContract.bind(paymentController)));
router.get('/client/:clientId', asyncHandler(paymentController.getPaymentsByClient.bind(paymentController)));
router.get('/:id', asyncHandler(paymentController.getPaymentById.bind(paymentController)));
router.get('/:id/receipt', asyncHandler(paymentController.generateReceipt.bind(paymentController)));

router.post('/', asyncHandler(paymentController.createPayment.bind(paymentController)));
router.post('/bulk-update', asyncHandler(paymentController.bulkUpdatePayments.bind(paymentController)));
router.post('/:id/confirm', asyncHandler(paymentController.confirmPayment.bind(paymentController)));
router.post('/:id/reminder', asyncHandler(paymentController.sendPaymentReminder.bind(paymentController)));

router.put('/:id', asyncHandler(paymentController.updatePayment.bind(paymentController)));
router.put('/:id/status', asyncHandler(paymentController.updatePaymentStatus.bind(paymentController)));

router.delete('/:id', asyncHandler(paymentController.deletePayment.bind(paymentController)));

module.exports = router;